<template>
  <div>
    <!-- 头部搜索栏 -->
    <van-row class="topNav">
      <van-col span="2">
        <van-icon name="arrow-left" @click="$router.go(-1)" />
      </van-col>
      <van-col span="22">
        <van-search placeholder="请输入搜索关键词" @focus="goSearch" />
      </van-col>
    </van-row>
    <!-- 左侧分类导航栏  -->
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" >
      <van-list class="categoriesList">
        <van-cell v-for="(item,index) in categories" :key="item" :title="item" @click="currentKey(item,index)" :style="{color: item===currentItem? 'orange':''}" />
      </van-list>
    </van-pull-refresh>
    <!-- 右侧商品栏 -->
    <div class="thumimages">
        <ul >
          <li v-for="(item) in goodsList" :key="item.id" @click="goGoodsDetail(item.id)">
            <img :src="item.img_url" />
            <p>{{item.title}}</p>
          </li>
          <li v-if="goodsList.length%3 == 2"></li>
          <template v-else-if="goodsList.length%3 == 1">
            <li></li>
            <li></li>
          </template>
        </ul>
    </div>

    <g-tabbar />
  </div>
</template>

<script>
import GTabbar from '../tabbar'
export default {
  data () {
    return {
      // 商品栏位移量
      translateYNum: 0,
      // 下拉刷新控制变量
      refreshing: false,
      currentItem: '热门推荐',
      categories: ['热门推荐', '手机数码',
        '家用电器',
        '电脑办公',
        '计生情趣',
        '美妆护肤',
        '个护清洁',
        '汽车生活',
        '京东超市',
        '男装',
        '男鞋',
        '女装',
        '女鞋',
        '母婴童装',
        '图书音像',
        '运动户外',
        '内衣配饰',
        '食品生鲜',
        '酒水饮料',
        '家具家装',
        '家居厨具',
        '箱包手袋',
        '钟表珠宝',
        '玩具乐器',
        '医药保健',
        '宠物生活',
        '礼品鲜花',
        '农资绿植',
        '生活旅行',
        '奢侈品',
        '京东国际',
        '艺术邮币',
        '二手商品',
        '特产馆',
        '京东金融',
        '国际名牌',
        '拍卖',
        '房产',
        '工业品'],
      pageindex: 1,
      goodsList: []
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    // 前往搜索页面
    goSearch () {
      this.$router.push('/search')
    },
    // 分类栏下拉刷新
    onRefresh () {
      setTimeout(() => {
        this.refreshing = false
      }, 1000)
    },
    // 当前选中分类高亮并获取当前分类商品列表
    currentKey (item, index) {
      this.pageindex = index + 1
      this.getGoodsList()
      this.currentItem = item
    },
    // 获取商品列表数据
    async getGoodsList () {
      const res = await this.$http.get(`/api/getgoods?pageindex=${this.pageindex}`)
      if (res.status !== 0) return this.$toast.fail('商品数据获取失败')
      console.log(res)

      this.goodsList = res.message
    },
    // 跳转商品详情列表
    goGoodsDetail (id) {
      this.$router.push('/goods/detail/' + id)
    }
    // 手指移动事件
    // move (e) {
    //   // console.log(e)

    //   this.translateYNum = `translateY(${e.target.offsetTop}px)`
    //   console.log(this.translateYNum)
    // }
  },
  components: {
    GTabbar
  }
}
</script>

<style lang="less" scoped>
.van-pull-refresh {
  width: 110px;
}
.topNav {
  width: 100%;
  height: 55px;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  margin-bottom: 46px;
  line-height: 60px;
  font-size: 20px;
  padding-left: 10px;
  background-color: #fff;
  border: 1px solid #d9dee0;
  // .van-search {
  //   border-bottom: 1px solid #ccc;
  // }
}
.categoriesList {
  position: relative;
  width: 100px;
  top: 60px;
}
.thumimages {
  width: 280px;
  height: auto;
  min-height: 100%;
  position: absolute;
  top: 100px;
  right: 0px;
  margin-bottom: 60px;
  ul {
    display: flex;
    position: absolute;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
    li {
      margin-top: 30px;
      width: 30%;
      img {
        width: 100%;
        border-radius: 5px;
        box-shadow: 1px 0 8px 1px rgba(0, 0, 0, 0.3);
      }
    }
  }
}
</style>
